import React from 'react';
import {Link} from 'react-router-dom'
function Testimonials() {
  return (
    <section className="relative">

      <div className="max-w-6xl mx-auto px-4 sm:px-6">
        <div className="py-12 md:py-20">

          {/* Section header */}
          <div className="max-w-3xl mx-auto text-center ">
            <h2 className="h2 mb-4">Trade $TRIANGLE for a node, stake your node and earn passive income with high APR!</h2>
          </div>
          <br />
          <img data-aos="zoom-y-out" data-aos-delay="150" className="rounded transform animate-float" src={require('../images/nodes/4.png').default} style={{height:"400px",margin:"auto"}} />

          {window.user != undefined?
           <div style={{textAlign:"center"}}>
            <Link to="/nodes" className="cursor-pointer btn text-white bg-gray-900 hover:bg-gray-800 w-full sm:w-auto sm:ml-4">View nodes</Link>
            </div>
          : 
          <div style={{textAlign:"center"}}>
            <a  rel="noreferrer" href="https://metamask.io/download" target="_blank" className="btn-sm text-gray-200 bg-gray-900 hover:bg-gray-800">
              <span  className='cursor-pointer'>Download Metamask</span>
            </a>
          </div>
          }
         
        </div>
      </div>
    </section>
  );
}

export default Testimonials;